





















import Vue from 'vue'
import Record from '@/components/Record.vue'
import Paginator from '@/components/Paginator.vue'
import { Amiibo } from '../service'

/**
 * Category page.
 *
 * @remarks
 * Shows 10 records per page of selected category.
 */
export default Vue.extend({
  name: 'Category',
  data () {
    return {
      records: [],
      loading: false,
      currentPage: 1,
      pageAmount: 0
    }
  },
  watch: {
    currentPage: function () {
      if (this.$route.query.page !== this.currentPage.toString()) {
        this.$router.push({
          name: 'category',
          query: {
            page: this.currentPage.toString()
          }
        })
      }
    },
    $route: 'fetchData'
  },
  created () {
    this.fetchData()
  },
  methods: {
    /**
     * Gets data for the Category page.
     *
     * @remarks
     * Gets a selected range of items from Amiibo service and calculates the total of pages.
     */
    fetchData () {
      this.records = []
      this.currentPage = Number(this.$route.query.page || 1)
      let params: object = {
        type: this.$route.params.type
      }
      this.loading = true
      Amiibo.getEntries(params)
        .then(
          (data) => {
            this.pageAmount = Math.floor(data.amiibo.length / 10) + (data.amiibo.length % 10 === 0 ? 0 : 1)
            let start: number = (this.currentPage - 1) * 10
            let end: number = (this.currentPage - 1) * 10 + 10
            this.records = data.amiibo.slice(start, end)
            this.loading = false
          }
        )
        .catch(
          function (err) {
            console.error('Fetch error', err)
          }
        )
    }
  },
  components: {
    Record,
    Paginator
  }
})
